.popup__window {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  padding: 25px 22px;
  width: 282px;
  position: relative;
}

@media (min-width: 600px) {
  .popup__window {
    padding: 36px;
    width: 430px;
  }
}
