@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 400;
  font-display: swap;
  src: url(/static/media/Inter-Regular.dc131113.woff2) format("woff2"),
       url(/static/media/Inter-Regular.3ac83020.woff) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 500;
  font-display: swap;
  src: url(/static/media/Inter-Medium.75db5319.woff2) format("woff2"),
       url(/static/media/Inter-Medium.c0638bea.woff) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 900;
  font-display: swap;
  src: url(/static/media/Inter-Black.661569af.woff2) format("woff2"),
       url(/static/media/Inter-Black.d0b121f3.woff) format("woff");
}

.page {
  background-color: #000;
  width: 100%;
}

.page__section {
  margin: 0 auto;
  box-sizing: border-box;
  padding-left: 19px;
  padding-right: 19px;
}

@media (min-width: 918px) {
  .page__section {
    padding-left: 0;
    padding-right: 0;
    max-width: 880px;
  }
}

.logo {
  width: 104px;
  height: 25px;
}

@media (min-width: 768px) {
  .logo {
    width: 142px;
    height: 33px;
  }
}

.header {
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  padding: 28px 27px 26px;
}

@media (min-width: 768px) {
  .header {
    width: calc(100% - 24px);
    padding: 45px 0 36px;
  }
}

.cards {
  display: grid;
  grid-template-columns: 282px;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
  padding-top: 36px;
}

@media (min-width: 500px) {
  .cards {
    grid-template-columns: repeat(2, minmax(210px, 282px));
  }
}

@media (min-width: 768px) {
  .cards {
    grid-gap: 17px;
    gap: 17px;
    padding-top: 50px;
    grid-template-columns: repeat(3, minmax(210px, 282px));
  }
}

.card {
  position: relative;
  max-width: 282px;
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  display: grid;
  font-size: 24px;
  grid-template-columns: auto -webkit-min-content;
  grid-template-columns: auto min-content;
  grid-template-rows: auto 80px;
  align-items: center;
}

.card__img {
  width: 100%;
  height: 282px;
  object-fit: cover;
  /* aspect-ratio: 1; */
  grid-column: span 2;
  cursor: pointer;
  max-height: 282px;
}

@media (min-width: 500px) {
  .card__img {
    height: calc((100vw - 38px - 20px) / 2);
  }
}

@media (min-width: 768px) {
  .card__img {
    height: calc((100vw - 38px - 34px) / 3);
  }
}

@media (min-width: 918px) {
  .card__img {
    height: 282px;
  }
}

.card__title {
  font-family: 'Inter', 'Verdana', sans-serif;
  font-weight: 900;
  font-size: inherit;
  line-height: 1.2;
  color: #000;
  margin: 0 7px 0 21px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.card__like-box {
  margin-right: 20px;
  display: grid;
  justify-items: center;
  grid-row-gap: 3px;
  row-gap: 3px;
}

.card__like {
  width: 22px;
  height: 19px;
  background-image: url('data:image/svg+xml,%3Csvg width="22" height="19" viewBox="0 0 22 19" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M20.2991 9.78586C22.567 7.54338 22.567 3.90213 20.2991 1.68186C18.0311 -0.560619 14.3486 -0.560619 12.0806 1.68186L10.9804 2.792L9.88007 1.70406C7.61215 -0.560619 3.92957 -0.560619 1.6841 1.68186C0.583823 2.76979 0 4.21297 0 5.74496C0 7.27695 0.606277 8.72013 1.6841 9.80806L10.9804 19L20.2991 9.78586ZM1.4371 5.74496C1.4371 4.59042 1.8862 3.52469 2.71702 2.72539C3.5703 1.88168 4.67058 1.45983 5.77086 1.45983C6.87114 1.45983 7.97142 1.88168 8.8247 2.72539L10.9804 4.83465L13.136 2.70318C14.8201 1.03798 17.582 1.03798 19.2437 2.70318C20.0521 3.50248 20.5236 4.56822 20.5236 5.72276C20.5236 6.8773 20.0745 7.94303 19.2437 8.74233L10.9804 16.9351L2.71702 8.76453C1.90865 7.94303 1.4371 6.8773 1.4371 5.74496Z" fill="black"/%3E%3C/svg%3E%0A');
  cursor: pointer;
  transition: -webkit-transform .3s linear;
  transition: transform .3s linear;
  transition: transform .3s linear, -webkit-transform .3s linear;
  border: none;
  background-color: transparent;
  padding: 0;
}

.card__like:hover {
  opacity: .5;
}

@-webkit-keyframes like-animation{
  33% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes like-animation{
  33% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.card__like_active {
  background-image: url('data:image/svg+xml,%3Csvg width="22" height="19" viewBox="0 0 22 19" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath fill-rule="evenodd" clip-rule="evenodd" d="M20.2991 1.68186C22.567 3.90213 22.567 7.54338 20.2991 9.78586L10.9804 19L1.6841 9.80806C0.606277 8.72013 0 7.27695 0 5.74496C0 4.21297 0.583823 2.76979 1.6841 1.68186C3.92957 -0.560619 7.61215 -0.560619 9.88007 1.70406L10.9804 2.792L12.0806 1.68186C14.3486 -0.560619 18.0311 -0.560619 20.2991 1.68186Z" fill="black"/%3E%3C/svg%3E%0A');
  -webkit-animation: like-animation .4s;
          animation: like-animation .4s;
}

.card__like_active:hover {
  opacity: 1;
}

.card__like-number {
  font-family: 'Inter', 'Verdana', sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 1.23;
}

.card__del {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 18px;
  height: 19px;
  background-image: url('data:image/svg+xml,%3Csvg width="18" height="19" viewBox="0 0 18 19" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M2.45787 17.86C2.51882 18.52 3.06735 19 3.73778 19H14.2615C14.9319 19 15.4804 18.5 15.5414 17.86L16.7197 5.69995H1.27954L2.45787 17.86Z" fill="white"/%3E%3Cpath d="M16.7201 1.9H11.5801V1.26C11.5801 0.560001 11.0113 0 10.3002 0H7.72009C7.00903 0 6.44018 0.560001 6.44018 1.26V1.9H1.27991C0.568849 1.9 0 2.46 0 3.16C0 3.86001 0.568849 4.42001 1.27991 4.42001H16.7201C17.4312 4.42001 18 3.86001 18 3.16C18 2.46 17.4312 1.9 16.7201 1.9Z" fill="white"/%3E%3C/svg%3E%0A');
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: opacity .2s linear;
}

.card__del:hover {
  opacity: .6;
}

.profile {
  display: grid;
  justify-content: center;
  justify-items: center;
  padding-top: 42px;
}

@media (min-width: 768px) {
  .profile {
    grid-template-columns: -webkit-min-content 1fr -webkit-min-content;
    grid-template-columns: min-content 1fr min-content;
    justify-items: start;
    align-items: center;
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
  }
}

.profile__user-pic-box {
  overflow: hidden;
  border-radius: 50%;
  margin-bottom: 26px;
  width: 120px;
  height: 120px;
  position: relative;
}

@media (min-width: 768px) {
  .profile__user-pic-box {
    margin-bottom: 0;
  }
}

.profile__user-pic-box::before {
  content: '';
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  background-image: url('data:image/svg+xml,%3Csvg width="26" height="26" viewBox="0 0 26 26" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M26 3.45351L6.76981 22.7932L3.33585 19.2903L22.517 0L26 3.45351ZM0 26L5.10189 24.4706L1.52075 21.0171L0 26Z" fill="white"/%3E%3C/svg%3E%0A');
  background-position: center;
  background-size: 26px 26px;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity .3s;
}

.profile__user-pic-box:hover::before {
  opacity: 1;
}

.profile__user-pic {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile__name {
  margin: 0;
  color: #fff;
  font-family: 'Inter', 'Verdana', sans-serif;
  font-weight: 500;
  font-size: 27px;
  line-height: 1.2;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media (min-width: 768px) {
  .profile__name {
    text-align: left;
    font-size: 42px;
    line-height: 1.15;
  }
}

.profile__about {
  color: #fff;
  font-family: 'Inter', 'Verdana', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  text-align: center;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media (min-width: 768px) {
  .profile__about {
    text-align: left;
    font-size: 18px;
  }
}

.profile__user-info-box {
  display: grid;
  grid-template-columns: 1fr -webkit-min-content;
  grid-template-columns: 1fr min-content;
  grid-row-gap: 8px;
  row-gap: 8px;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  align-items: center;
  margin-bottom: 36px;
  padding-left: 28px;
}

@media (min-width: 768px) {
  .profile__user-info-box {
    margin-bottom: 0;
    grid-column-gap: 18px;
    -webkit-column-gap: 18px;
            column-gap: 18px;
  }
}

.profile__edit-btn {
  background-image: url('data:image/svg+xml,%3Csvg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M10 1.32827L2.60377 8.7666L1.28302 7.41936L8.66038 0L10 1.32827ZM0 10L1.96226 9.41177L0.584906 8.08349L0 10Z" fill="white"/%3E%3C/svg%3E%0A');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 8px 8px;
  width: 18px;
  height: 18px;
  background-color: transparent;
  box-sizing: border-box;
  border: 1px #fff solid;
  transition: opacity .2s linear;
  cursor: pointer;
}

.profile__edit-btn:hover {
  opacity: .6;
}

@media (min-width: 768px) {
  .profile__edit-btn {
    width: 24px;
    height: 24px;
    background-size: 10px 10px;
  }
}

.profile__add-card {
  background-image: url('data:image/svg+xml,%3Csvg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M16 7.11111H8.88889V0H7.11111V7.11111H0V8.88889H7.11111V16H8.88889V8.88889H16V7.11111Z" fill="white"/%3E%3C/svg%3E%0A');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  background-color: transparent;
  box-sizing: border-box;
  height: 50px;
  width: 282px;
  border: 2px #fff solid;
  border-radius: 2px;
  cursor: pointer;
  padding: 0;
  transition: opacity .2s linear;
}

.profile__add-card:hover {
  opacity: .6;
}

@media (min-width: 768px) {
  .profile__add-card {
    width: 150px;
  }
}

.popup {
  position: fixed;
  visibility: hidden;
  opacity: 0;
  display: grid;
  place-content: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, .5);
  z-index: 3;
  transition: opacity .4s, visibility 0s .4s;
}

.popup__window {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  padding: 25px 22px;
  width: 282px;
  position: relative;
}

@media (min-width: 600px) {
  .popup__window {
    padding: 36px;
    width: 430px;
  }
}

.popup_active {
  visibility: visible;
  opacity: 1;
  transition: visibility 0s, opacity .4s;
}


.popup__form {
  display: grid;
}

.popup__title {
  font-family: 'Inter', 'Verdana', sans-serif;
  font-weight: 900;
  font-size: 18px;
  line-height: 1.2;
  margin: 0 0 75px;
}

@media (min-width: 600px) {
  .popup__title {
    margin-bottom: 48px;
    font-size: 24px;
  }
}

.popup__input {
  font-family: 'Inter', 'Verdana', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  border: none;
  border-bottom: 1px solid #ccc;
  padding: 0 0 13px;
  outline: none;
  box-sizing: border-box;
}

.popup__input:focus {
  border-bottom: 1px solid dodgerblue;
}

.popup__input::-webkit-input-placeholder {
  opacity: .6;
}

.popup__input::placeholder {
  opacity: .6;
}

@media (min-width: 600px) {
  .popup__input {
    padding-bottom: 9px;
  }
}

.popup__input_type_error {
  border-bottom: 1px solid #f00;
}

.popup__input_type_error:focus {
  border-bottom: 1px solid #f00;
}

.popup__submit-btn {
  border: none;
  background-color: #000;
  color: #fff;
  font-family: 'Inter', 'Verdana', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  padding: 14px;
  cursor: pointer;
  transition: opacity .2s linear;
}

.popup__submit-btn:disabled {
  background-color: transparent;
  border: 1px solid #000000;
  opacity: .2;
  color: #000;
  cursor: auto;
}

.popup__submit-btn:disabled:hover {
  opacity: .2;
}

.popup__submit-btn:hover {
  opacity: .8;
}

@media (min-width: 600px) {
  .popup__submit-btn {
    font-size: 18px;
  }
}

.popup__close-btn {
  background-image: url('data:image/svg+xml,%3Csvg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M20 18L12 10L20 2L18 0L10 8L2 0L0 2L8 10L0 18L2 20L10 12L18 20L20 18Z" fill="white"/%3E%3C/svg%3E');
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  width: 20px;
  height: 20px;
  border: none;
  cursor: pointer;
  padding: 0;
  transition: opacity .2s linear;
  justify-self: end;
  position: absolute;
  top: -36px;
  right: 0;
}

.popup__close-btn:hover {
  opacity: .6;
}

@media (min-width: 600px) {
  .popup__close-btn {
    width: 32px;
    height: 32px;
    background-size: 32px 32px;
    right: -40px;
    top: -40px;
  }
}

.popup__input-error {
  font-family: 'Inter', 'Verdana', sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.25;
  color: #f00;
  min-height: 30px;
  margin-bottom: 0px;
  margin-top: 5px;
  visibility: hidden;
}

.popup__input-error:last-of-type {
  margin-bottom: 17px;
}

.popup__input-error_active {
  visibility: visible;
}

.card-image {
  position: relative;
}

.card-image__box {
  margin: 0;
}

.card-image__img {
  display: block;
  margin-bottom: 10px;
  max-width: 75vw;
  max-height: 75vh;
}

.card-image__caption {
  font-family: 'Inter', 'Verdana', sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.25;
  color: #fff;
}

.footer {
  padding-top: 48px;
  padding-bottom: 36px;
}

@media (min-width: 600px) {
  .footer {
    padding-top: 66px;
    padding-bottom: 60px;
  }
}

.footer__text {
  margin: 0;
  color: #545454;
  font-family: 'Inter', 'Verdana', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
}

@media (min-width: 600px) {
  .footer__text {
    font-size: 18px;
  }
}



